<template>
  <container :title="title">
    <list :editRoute="configRoute">
      <thead slot="thead">
      <tr>
        <th>{{lang.id}}</th>
        <th>{{lang.key}}</th>
        <th>{{lang.value}}</th>
        <th>{{lang.description}}</th>
        <th>{{lang.type}}</th>
        <th width="125">{{$t('operation.name')}}</th>
      </tr>
      </thead>
      <tbody slot="tbody">
      <tr v-for="vol in list">
        <td>{{vol.id}}</td>
        <td>{{vol.key}}</td>
        <td>{{vol.value}}</td>
        <td>{{vol.description}}</td>
        <td>{{vol.type}}</td>

        <v-td type="opt" :value="vol.id"
              edit-route="sys_config_edit" />
      </tr>
      </tbody>
    </list>
  </container>
</template>

<script>
  import {Container,List, VTd} from "@/components"
  import Checkbox from "../../components/forms/Checkbox"

  export default {

    components: {Checkbox, VTd,Container, List},
    data(){
      return{
        title:this.$t('config.index'),
        lang:this.$t('config'),
        configRoute:{
          name:"sys_config_edit"
        }
      }
    },
    computed:{
      list(){
        return this.$store.state.table.list
      }
    },
    mounted(){
      const url = '/api/config/index'
      this.$store.commit('table/SET_URL', url)
      this.$store.dispatch('table/fetchList')
    }
  }
</script>



